

/**
 * Function that groups an array of object, based normally on a property
 * in the objects byt this time we building our own key to group by
 * @param edges
 * @returns {*}
 */
export function groupByDate(edges) {
  return edges.reduce((acc, edge) => {
    const { node: { frontmatter: { date } } } = edge
    const key = date

    if (!acc[key]) {
      acc[key] = []
    }

    acc[key].push({ ...edge.node.fields, ...edge.node.frontmatter })

    return acc
  }, {})
}
